@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background-color: #fff!important;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
  overflow: hidden;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.auth-wrapper {
  display: inline-block;
}

.logo {
  margin: 70px 0 50px;
}

.login a {
  color: #fff
}

.detail {
  background: #fff;
  margin: 0 auto;
  margin-top: 30px;
  padding: 50px;
  text-align: left;
  width: 50%;
  width: 750px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgb(34 35 58 / 20%);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.detail h3 {
  color: rgb(255, 204, 0);
  text-align: center;
  margin: 30px;
}

.detail .detail-content {
  width: 80%;
  margin: 0 auto;
}

.detail .detail-content .form-group {

}

.detail .detail-content .form-group label {
    margin-right: 20px; 
    width: 95px;
}

.detail .detail-content .form-group input {
    /* width: 65%; */
    width: 300px;
}

.txtCombobox {
    /* width: 65%; */
    width: 300px;
}

.offer-combobox .distance span {
  margin-left: 10px;
  display: inline-block;
}

.btn.btn-primary.btn-default,
.btn.btn-primary.btn-default:hover,
.btn.btn-primary.btn-default:focus,
.btn.btn-primary.btn-default:active {
  color: #fff;
  background: rgb(255, 204, 0);
  outline: none;
  border: none;
  width: auto;
  box-shadow: none;
  margin: 0 auto;
  padding: 7px 30px;
}

.background-white {
  background-color: #fff;
}
#main-wrapper {
  margin: 10px auto;
  box-sizing: border-box;
}

#main-wrapper .container{
  text-align: left;
}
.list-group {
  border: none;
  border-radius: 0;
}
.list-group .list-group-item {
  color: black;
  border: 0;
  /* border-bottom: 1px solid #fdc600; */
  font-weight: bold;
  padding-left: 0;
}

.list-group-item span {
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
}
.list-group .list-group-item:hover {
  text-decoration: underline;
  color: rgb(255, 204, 0);
}
#main-wrapper .container .dashboard-list {
  padding: 15px;
}
#main-wrapper .container .btn-add {
  width:100%;
  margin: 10px auto;
  background-color: rgb(255, 204, 0);
  border: 0;
  font-weight: bold;
}
#main-wrapper .container .title-wrapper {
  border-bottom: 3px solid rgb(255, 204, 0);
}
#main-wrapper .container .title-wrapper .title {
  margin-top: 5px;
}

.icon {
  padding-right: 8px;
  color: rgb(255, 204, 0);
  font-size: 28px;
}

.btn.btn-primary.btn-default.btn-save {
  margin-left: 115px;
}
